import React, { Component } from "react";
import { connect } from "react-redux";
import SearchResult from "./searchresult";
import { withRouter } from "react-router-dom";
import Loader from "react-loader-spinner";
import { fetchArtistSearchResult } from "../../redux/store";
import { fetchAlbumsSearchResult } from "../../redux/store";

class SearchResultList extends Component {
  componentDidMount() {
    fetchArtistSearchResult(localStorage.getItem("searchTerm"));
    fetchAlbumsSearchResult(localStorage.getItem("searchTerm"));

    if (this.props.artistSearchResult.length === 0) {
      this.props.history.replace({ pathname: "/" });
    }
    if (this.props.user.userInfo === null) {
      this.props.history.replace({ pathname: "/login" });
    }
  }

  render() {
    const isFetchedArtists = this.props.artistSearchResult.isFetched;
    const isFetchedAlbums = this.props.albumSearchResult.isFetched;

    if (isFetchedArtists === false && isFetchedAlbums === false) {
      return (
          <div className="loader-container">
            <Loader type="Audio" color="#black" height="250" width="250" />
          </div>
      );
    } else {
      return (
        <div className="display-search-results">
          <div className="search-section1">
            <div className="search-section-title">Artists</div>
            <div className="search-result-container">
              {this.props.artistSearchResult.data.map((item, i) => (
                <SearchResult
                  type={"artist"}
                  name={item.name}
                  listeners={item.listeners}
                  key={i}
                  image={item.image[2]["#text"]}
                />
              ))}
            </div>
          </div>

          <div className="search-section">
            <div className="search-section-title">Albums</div>
            <div className="search-result-container">
              {this.props.albumSearchResult.data.map((item, i) => (
                <SearchResult
                  type={"album"}
                  artist={item.artist}
                  name={item.name}
                  listeners={item.listeners}
                  key={i}
                  image={item.image[2]["#text"]}
                />
              ))}
            </div>
          </div>
        </div>
      );
    }
  }
}
const mapStateToProps = state => ({
  user: state.user,
  artistSearchResult: state.artistSearchResult || {},
  albumSearchResult: state.albumSearchResult || {}
});

const SearchResContainer = connect(
  mapStateToProps,
  null
)(SearchResultList);

export default withRouter(SearchResContainer);
