import React, { Component } from "react";
import "antd/dist/antd.css";
import { Icon } from "antd";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { deleteUser } from "../../redux/store";

class HeaderMenu extends Component {
  handleLogOut() {
    const user = this.props.userObj.userInfo;
    if (user !== null) {
      this.props.clear();
      localStorage.removeItem("user");
      localStorage.removeItem("searchTerm");
      this.props.history.replace({ pathname: "/login" });
    } else {
      this.props.history.replace({ pathname: this.props.location.pathname });
    }
  }

  handleLogin() {
    const username = localStorage.getItem("user");
    if (username !== null) {
      this.props.history.replace({ pathname: this.props.location.pathname });
    } else {
      this.props.history.replace({ pathname: "/login" });
    }
  }

  render() {
    return (
      <div>
        <div className="header">
          <div className="icons">
            <div>
              <Icon type="fast-backward" className="music-icons" />
            </div>

            <div>
              <Icon type="play-circle" className="music-icons" />
            </div>
            <div>
              <Icon type="fast-forward" className="music-icons" />
            </div>
            <div className="music-icons">{this.props.user}</div>
          </div>
          <a className="header-center" href={"http://localhost:3000"}>
            <img
              className="header-logo"
              src={
                "https://upload.wikimedia.org/wikipedia/commons/thumb/d/d4/Lastfm_logo.svg/1280px-Lastfm_logo.svg.png"
              }
              alt={""}
            />
          </a>
          <div className="header-menu">
            <div>
              <Icon
                type="search"
                className="search-icon"
                theme=""
                onClick={() => this.props.change(true)}
              />
            </div>
            <div>
              <Link to={"/"} style={{ textDecoration: "none" }}>
                <span className="menu-item">
                  <span>HOME</span>
                </span>
              </Link>
            </div>
            {this.props.userObj.userInfo !== null && (
              <div>
                <span className="menu-item" onClick={() => this.handleLogOut()}>
                  LOGOUT <Icon type="logout" />
                </span>
              </div>
            )}
            {this.props.userObj.userInfo === null && (
              <div>
                <span className="menu-item" onClick={() => this.handleLogin()}>
                  <Icon type="login" /> LOGIN
                </span>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    userObj: state.user || {}
  };
};
const mapDispatchToProps = dispatch => {
  return {
    clear: () => {
      dispatch(deleteUser());
    }
  };
};

const WrappedHeaderMenu = connect(
  mapStateToProps,
  mapDispatchToProps
)(HeaderMenu);

export default withRouter(WrappedHeaderMenu);
