import React, { Component } from "react";
import TopArtist from "./topartist";
import SecondArtist from "./secondartist";
import { connect } from "react-redux";
import SmallArtistsList from "./smallartistslist";

class Artists extends Component {
  render() {
    const artists = this.props.artists;
    const topArtist = artists[0];
    const secondArtist = artists[1];
    const row1 = [artists[2], artists[3], artists[4]];
    const row2 = [artists[5], artists[6], artists[7]];
    const row3 = [artists[8], artists[9], artists[10]];
    const row4 = [artists[11], artists[12], artists[13]];

    return (
      <div className="app-container">
        <div className="section-title">
          <div className="title">
            <div className="title-string">Artists for you</div>
            <div className="line" />
          </div>
        </div>
        <div className="artists-container">
          <div className="topartists-container" />
          <TopArtist
            name={topArtist.name}
            image={topArtist.image[4]["#text"]}
          />
          <SecondArtist
            name={secondArtist.name}
            image={secondArtist.image[4]["#text"]}
          />
        </div>
        <div className="tags-container1">
          <div className="grid-container">
            <div className="row">
              <SmallArtistsList artists={row1} />
            </div>
            <div className="row">
              <SmallArtistsList artists={row2} />
            </div>
          </div>
          <div className="grid-container1">
            <div className="row">
              <SmallArtistsList artists={row3} />
            </div>
            <div className="row">
              <SmallArtistsList artists={row4} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  artists: state.artists.topartists || []
});

const ArtistsContainer = connect(
  mapStateToProps,
  null
)(Artists);

export default ArtistsContainer;
