import React, { Component } from "react";
import Tags from "./Tags/tags";
import Artists from "./Artists/artist";
import Loader from "react-loader-spinner";
import { connect } from "react-redux";
import { fetchArtists } from "../redux/store";
import { fetchTopArtistsInfo } from "../redux/store";
import { fetchTags } from "../redux/store";
import { fetchTopTagsBio } from "../redux/store";
import { fetchTagTopArtists } from "../redux/store";

class HomePageContent extends Component {
  state = {
    loadingArtists: true,
    loadingTags: true
  };

  requestArtistsInfo() {
    fetchArtists().then(() =>
      Promise.all([
        fetchTopArtistsInfo(this.props.artists[0].name),
        fetchTopArtistsInfo(this.props.artists[1].name)
      ]).then(() => {
        this.setState({
          loadingArtists: false
        });
      })
    );
  }

  requestTagsInfo() {
    fetchTags().then(() => {
      Promise.all([
        fetchTopTagsBio(this.props.tags[0].name),
        fetchTagTopArtists(this.props.tags[0].name),
        fetchTopTagsBio(this.props.tags[1].name),
        fetchTagTopArtists(this.props.tags[1].name)
      ]).then(() => {
        this.setState({
          loadingTags: false
        });
      });
    });
  }

  componentDidMount() {
    this.requestArtistsInfo();
    this.requestTagsInfo();
  }

  render() {
    if (
      this.state.loadingArtists === false &&
      this.state.loadingTags === false
    ) {
      return (
        <div>
          {this.props.user.userInfo !== null && (
            <div className="logged-user">
              <b>Welcome</b>
              <span className="username-homepage">
                {this.props.user.userInfo.username}
              </span>
              !
            </div>
          )}

          <Artists />
          <Tags />
        </div>
      );
    } else {
      return (
        <div className="loader-container">
          <Loader type="Audio" color="#black" height="250" width="250" />
        </div>
      );
    }
  }
}

const mapStateToProps = state => ({
  user: state.user || {},
  artists: state.artists.topartists || [],
  tags: state.tags.toptags || []
});

const HomePageContainer = connect(
  mapStateToProps,
  null
)(HomePageContent);

export default HomePageContainer;
