export const bioFormater = str => {
  const firstSentence = str.split(".")[0] + ".";
  const secondSentence = str.split(".")[1];
  return (
    [firstSentence, secondSentence].join("") + "..."
  );
};

export const numberFormater = (number) => {
  let r, i;
	r = "" + number;
	for (i = r.length - 3; i > 0; i -= 3) {
		r = r.substr(0, i) + "," + r.substr(i);
	}
	return r;
}