import React from "react";
import { numberFormater } from '../../helpers/workmates';

const SmallArtistContainer = props => {
  const style = {
    background: `url(${props.image})`,
    backgroundSize: "cover"
  };
  return (
    <div className="col-sm-4 col-xs-4">
      <div className="artist-element" style={style} />
      <div className="smallartist-name">{props.name}</div>
      <div className="smallartist-similar">Playcount: {numberFormater(props.playcount)}</div>
    </div>
  );
};

export default SmallArtistContainer;
