import React from "react";
import { numberFormater } from "../../helpers/workmates";

const SearchResult = props => {
  const style = {
    background: `repeating-linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.67),
        rgba(0, 0, 0, 0.67)
      ), 
      url(${props.image})`,
    backgroundSize: "cover"
  };
  return (
    <div className="col-lg-2 col-md-3 col-sm-4 col-xs-6" style={style}>
      <div className="search-result">
        <div className="search-artist-name">{props.name}</div>
        <div className="search-artist-listeners">
          {props.type === "artist"
            ? numberFormater(props.listeners) + " listeners"
            : props.artist}
        </div>
      </div>
    </div>
  );
};

export default SearchResult;
