import React, { Component } from "react";
import { connect } from "react-redux";
import { bioFormater } from "../../helpers/workmates";
import { numberFormater } from '../../helpers/workmates';

class SecondArtist extends Component {
  state = {
    bio: bioFormater(
      this.props.artistInfo[this.props.name].bio.summary.split(" <a")[0]
    ),
    read: "more"
  };

  displayAllBio() {
    if (this.state.read === "more") {
      this.setState({
        bio: this.props.artistInfo[this.props.name].bio.summary.split(" <a")[0],
        read: "less"
      });
    } else {
      this.setState({
        bio: bioFormater(
          this.props.artistInfo[this.props.name].bio.summary.split(" <a")[0]
        ),
        read: "more"
      });
    }
  }

  render() {
    const similarTo = this.props.artistInfo[this.props.name].similar.artist[0]
      .name;
    const tag = this.props.artistInfo[this.props.name].tags.tag;
    const artistTags = [tag[0].name, tag[1].name, tag[2].name];
    const playcount = this.props.artistInfo[this.props.name].stats.playcount;
    const listeners = this.props.artistInfo[this.props.name].stats.listeners;
    const style = {
      background: `repeating-linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.67),
      rgba(0, 0, 0, 0.67)
    ), 
    url(${this.props.image})`,
      backgroundSize: "cover"
    };
    return (
      <div className="top-artist2-section1" style={style}>
        <div className="artist-info">
          <div className="tag-link">
            <a href={"localhost:3000"} alt={""} style={{ color: "white", fontSize: "15pt" }}>
              {this.props.name}
            </a>
          </div>
          <div className="similar-to">
            Similar to <span className="similar-to-artist">{similarTo}</span>
          </div>
          <div className="artist-tags">
            {artistTags.map((item, i) => (
              <div className="artist-tag" key={i}>
                {<div className="tag">{item}</div>}{" "}
              </div>
            ))}
          </div>
          <div className="bio">
            {this.state.bio}
            <span className="readmore">
              <span className="read" onClick={() => this.displayAllBio()}>
                read {this.state.read}
              </span>
            </span>
          </div>
          <div className="artist-stats">
            <div className="playcount">
              Playcount: <span className="numbers">{numberFormater(playcount)}</span>
            </div>
            <div className="listeners">
              Listeners: <span className="numbers">{numberFormater(listeners)}</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  artistInfo: state.artistsInfo || {}
});

const SecondArtistContainer = connect(
  mapStateToProps,
  null
)(SecondArtist);

export default SecondArtistContainer;
