import React from "react";
import { connect } from "react-redux";
import "antd/dist/antd.css";
import "./index.css";
import { Form, Icon, Input, Button, Checkbox, Alert } from "antd";
import { storeUser } from "../../redux/store";

class NormalLoginForm extends React.Component {
  state = {
    username: "",
    password: "",
    authenticationStatus: "",
    loggedIn: null
  };
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);
      }
    });
  };

  handleLogin() {
    const user = {
      username: this.state.username,
      password: this.state.password
    };

    const authenticationChances = [
      "succeed",
      "succeed",
      "failed",
      "succeed",
      "succeed",
      "succeed",
      "failed",
      "succeed",
      "succeed",
      "failed",
      "succeed"
    ];

    this.setState({
      authenticationStatus:
        authenticationChances[
          Math.floor(Math.random() * authenticationChances.length)
        ]
    });

    if (this.state.username !== "" && this.state.password !== "") {
      //Set loading to true to simulate login
      this.props.submitNewUser(null, true);
      setTimeout(() => {
        if (this.state.authenticationStatus === "succeed") {
          localStorage.setItem("user", JSON.stringify(user));
          const userObj = JSON.parse(localStorage.getItem("user"));
          //storing userObj in redux and setting loading to false
          this.props.submitNewUser(userObj, false);
          this.setState({
            loggedIn: true
          });
          this.props.history.replace({ pathname: "/" });
        } else {
          this.props.submitNewUser(null, false);
          this.setState({
            loggedIn: false
          });
        }
      }, 2000);
    } else {
      this.props.submitNewUser(null, false);
      this.setState({
        loggedIn: false
      });
    }
  }
  // Loading is set to false after the simualted network request has ended

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="login-container">
        <div className="login">
          {" "}
          <div className="login-label">Login</div>
          <Form onSubmit={this.handleSubmit} className="login-form">
            <Form.Item>
              {getFieldDecorator("username", {
                rules: [
                  { required: true, message: "Please input your username!" },
                  {
                    pattern: /[a-zA-Z]/gi,
                    message: "Please insert only letters!"
                  }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder="Username"
                  className="username"
                  style={{ height: "40px" }}
                  onChange={event =>
                    this.setState({ username: event.target.value })
                  }
                />
              )}
            </Form.Item>
            <Form.Item>
              {getFieldDecorator("password", {
                rules: [
                  { required: true, message: "Please input your Password!" }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  type="password"
                  placeholder="Password"
                  className="password"
                  style={{ height: "40px" }}
                  onChange={event =>
                    this.setState({ password: event.target.value })
                  }
                />
              )}
            </Form.Item>
            <Form.Item>
              {getFieldDecorator("remember", {
                valuePropName: "checked",
                initialValue: true
              })(<Checkbox />)}
              <span className="remember-me">Remember me</span>
              <a
                className="login-form-forgot"
                href="http://localhost:3000/login"
              >
                Forgot password
              </a>
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button"
                style={{
                  backgroundColor: "#d50000",
                  borderColor: "#ef1a1a",
                  fontSize: "12pt",
                  fontWeight: "450"
                }}
                onSubmit={() => this.handleLogin()}
                onClick={() => this.handleLogin()}
              >
                {this.props.user.loading === false && <Icon type="login" />}
                {this.props.user.loading === true && <Icon type="loading" />}
                Sign in
              </Button>
              Or{" "}
              <a
                style={{ cursor: "pointer" }}
                href="http://localhost:3000/login"
              >
                register now!
              </a>
              {this.state.loggedIn === false && (
                <Alert
                  style={{
                    borderColor: "red",
                    borderStyle: "solid",
                    borderWidth: "3px",
                    backgroundColor: "whitesmoke"
                  }}
                  message="Authentication Failed."
                  description="Wrong credentials."
                  type="error"
                  showIcon
                />
              )}
            </Form.Item>
          </Form>
        </div>
      </div>
    );
  }
}

const WrappedNormalLoginForm = Form.create({ name: "normal_login" })(
  NormalLoginForm
);

const mapStateToProps = state => {
  return { user: state.user };
};

const mapDispatchToProps = dispatch => {
  return {
    submitNewUser: (user, status) => {
      dispatch(storeUser(user, status));
    }
  };
};
const LoginForm = connect(
  mapStateToProps,
  mapDispatchToProps
)(WrappedNormalLoginForm);

export default LoginForm;
