import React from "react";
import SmallArtistContainer from "./smallartistcontainer";

const SmallArtistsList = props => {
  return (
    <div>
      {props.artists.map((item, i) => (
        <SmallArtistContainer
          name={item.name}
          key={i}
          playcount={item.playcount}
          image={item.image[2]["#text"]}
        />
      ))}
    </div>
  );
};

export default SmallArtistsList;
