/* eslint-disable no-use-before-define */
import { combineReducers, createStore } from "redux";
import axios from "axios";

const FETCH_TAGS = "FETCH_TAGS";
const FETCH_ARTISTS = "FETCH_ARTISTS";
const FETCH_ARTISTS_INFO = "FETCH_ARTISTS_INFO";
const FETCH_ARTISTS_SEARCH_RESULT = "FETCH_ARTISTS_SEARCH_RESULT";
const FETCH_ALBUMS_SEARCH_RESULT = "FETCH_ALBUMS_SEARCH_RESULT";
const FETCH_BIO = "FETCH_BIO";
const FETCH_TAG_TOP_ARTISTS = "FETCH_TAG_TOP_ARTISTS";
const LOGIN = "LOGIN";
const LOGOUT = "LOGOUT";

export const storeUser = (user, status) => {
  return {
    type: LOGIN,
    user,
    loading: status
  };
};

export const deleteUser = () => {
  return {
    type: LOGOUT,
    user: null,
    loading: false
  };
};

const initial = {
  userObj: {
    userInfo: JSON.parse(localStorage.getItem("user")),
    loading: false
  },
  artistSearchResult: {
    data: [],
    isFetched: false,
    searchTerm: ""
  },
  albumSearchResult: {
    data: [],
    isFetched: false,
    searchTerm: ""
  },
  artists: {},
  artistsInfo: {},
  tags: {},
  tagsBio: {},
  tagsTopArtists: {}
};

const API_KEY = `32294161a44c0621caf3a01a6e94b1a5`;

export const fetchArtists = () =>
  axios
    .get(
      `https://ws.audioscrobbler.com/2.0/?method=user.gettopartists&user=rj&api_key=${API_KEY}&format=json`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_ARTISTS,
        data: response.data.topartists.artist
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_ARTISTS,
        data: error.response
      });
    });

export const fetchTopArtistsInfo = artistName =>
  axios
    .get(
      `https://ws.audioscrobbler.com/2.0/?method=artist.getinfo&artist=${artistName}&api_key=${API_KEY}&format=json`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_ARTISTS_INFO,
        name: artistName,
        data: response.data.artist
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_ARTISTS_INFO,
        data: error.response
      });
    });

export const fetchArtistSearchResult = artistName =>
  axios
    .get(
      `https://ws.audioscrobbler.com/2.0/?method=artist.search&artist=${artistName}&api_key=${API_KEY}&format=json&limit=12`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_ARTISTS_SEARCH_RESULT,
        name: artistName,
        data: response.data.results.artistmatches.artist,
        isFetched: true
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_ARTISTS_SEARCH_RESULT,
        data: error.response
      });
    });

export const fetchAlbumsSearchResult = albumName =>
  axios
    .get(
      `https://ws.audioscrobbler.com/2.0/?method=album.search&album=${albumName}&api_key=${API_KEY}&format=json&limit=12`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_ALBUMS_SEARCH_RESULT,
        name: albumName,
        data: response.data.results.albummatches.album,
        isFetched: true
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_ALBUMS_SEARCH_RESULT,
        data: error.response
      });
    });
export const fetchTags = () =>
  axios
    .get(
      `https://ws.audioscrobbler.com/2.0/?method=user.gettoptags&user=rj&api_key=${API_KEY}&format=json`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_TAGS,
        data: response.data.toptags.tag
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_TAGS,
        data: error.response
      });
    });

export const fetchTopTagsBio = tagName =>
  axios
    .get(
      `https://ws.audioscrobbler.com//2.0/?method=tag.getinfo&tag=${tagName}&api_key=${API_KEY}&format=json`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_BIO,
        name: tagName,
        data: response.data.tag.wiki.summary
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_BIO,
        data: error.response
      });
    });

export const fetchTagTopArtists = tagName =>
  axios
    .get(
      `https://ws.audioscrobbler.com/2.0/?method=tag.gettopartists&tag=${tagName}&api_key=${API_KEY}&format=json`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_TAG_TOP_ARTISTS,
        name: tagName,
        data: response.data.topartists.artist
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_TAG_TOP_ARTISTS,
        data: error.response
      });
    });

const fetchArtistsReducer = (state = initial.artists, action) => {
  switch (action.type) {
    case FETCH_ARTISTS:
      return {
        ...state,
        topartists: action.data
      };
    default:
      return state;
  }
};

const fetchTagsReducer = (state = initial.tags, action) => {
  switch (action.type) {
    case FETCH_TAGS:
      return {
        ...state,
        toptags: action.data
      };
    default:
      return state;
  }
};

const fetchTagsBioReducer = (state = initial, action) => {
  switch (action.type) {
    case FETCH_BIO:
      return {
        tagsBio: {
          ...state.tagsBio,
          [action.name]: action.data.split(" <a")[0]
        }
      };
    default:
      return state;
  }
};

const fetchTopArtistsInfoReducer = (state = initial.artistsInfo, action) => {
  switch (action.type) {
    case FETCH_ARTISTS_INFO:
      return {
        ...state,
        [action.name]: action.data
      };
    default:
      return state;
  }
};

const fetchArtistsSearchResultsReducer = (
  state = initial.artistSearchResult,
  action
) => {
  switch (action.type) {
    case FETCH_ARTISTS_SEARCH_RESULT:
      return {
        ...state,
        data: action.data,
        isFetched: action.isFetched,
        searchTerm: action.name
      };
    default:
      return state;
  }
};

const fetchAlbumsSearchResultsReducer = (
  state = initial.albumSearchResult,
  action
) => {
  switch (action.type) {
    case FETCH_ALBUMS_SEARCH_RESULT:
      return {
        ...state,
        data: action.data,
        isFetched: action.isFetched,
        searchTerm: action.name
      };
    default:
      return state;
  }
};

const fetchTagTopArtistReducer = (state = initial.tagsTopArtists, action) => {
  switch (action.type) {
    case FETCH_TAG_TOP_ARTISTS:
      return {
        ...state,
      [action.name]: action.data
      };
    default:
      return state;
  }
};

const userRecducer = (state = initial.userObj, action) => {
  switch (action.type) {
    case LOGIN:
      return {
        ...state,
        userInfo: action.user,
        loading: action.loading
      };
    case LOGOUT:
      return {
        ...state,
        userInfo: action.user,
        loading: action.loading
      };

    default:
      return state;
  }
};
export const rootReducer = combineReducers({
  user: userRecducer,
  artistSearchResult: fetchArtistsSearchResultsReducer,
  albumSearchResult: fetchAlbumsSearchResultsReducer,
  artists: fetchArtistsReducer,
  artistsInfo: fetchTopArtistsInfoReducer,
  tags: fetchTagsReducer,
  bio: fetchTagsBioReducer,
  tagTopArtists: fetchTagTopArtistReducer
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;

let store = null;

if (process.env.NODE_ENV === "production") {
  store = createStore(rootReducer);
} else {
  store = createStore(rootReducer, composeEnhancers());
}
// fetchAlbumsSearchResult("meek").then(() =>
//   console.log(store.getState().albumSearchResult)
// );
export default store;
