import React, { Component } from "react";
import { connect } from "react-redux";
import { bioFormater } from "../../helpers/workmates";
import ArtistSmallGrid from './artistssmallgrid';
class TopArtist extends Component {
  state = {
    bio: bioFormater(this.props.bio[this.props.tagName]),
    read: "more"
  };
  displayAllBio() {
    if (this.state.read === "more") {
      this.setState({
        bio: this.props.bio[this.props.tagName],
        read: "less"
      });
    } else {
      this.setState({
        bio: bioFormater(this.props.bio[this.props.tagName]),
        read: "more"
      });
    }
  }
  render() {
    const tagTopArtists = this.props.tagTopArtists[this.props.tagName];
    const row1 = [tagTopArtists[1], tagTopArtists[2]];
    const row2 = [tagTopArtists[3], tagTopArtists[4]];

    const topArtistStyle = {
      background: `repeating-linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.67),
        rgba(0, 0, 0, 0.67)
      ), 
      url(${tagTopArtists[0].image[4]["#text"]})`,
      backgroundSize: "cover"
    };
    return (
      <div className="top-artist" style={topArtistStyle}>
        <div className="tags-artist-info">
          <div className="tag-link">
            <a href={"https://google.com"} alt={""} style={{ color: "white", fontSize: "15pt" }}>
              {this.props.tagName.toUpperCase()}
            </a>
          </div>
          <div>
            Featured Artist:{" "}
            <span style={{ fontWeight: "bold" }} className="artist-name">
              {tagTopArtists[0].name}
            </span>
          </div>
          <div className="bio">
            {this.state.bio + " "}
            <span className="readmore">
              <span className="read" onClick={() => this.displayAllBio()}>
                read {this.state.read}
              </span>
            </span>
          </div>
          <ArtistSmallGrid artistsRow1={row1} artistsRow2={row2}/>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  bio: state.bio.tagsBio || {},
  tagTopArtists: state.tagTopArtists || {}
});

const TagTopArtistContainer = connect(
  mapStateToProps,
  null
)(TopArtist);

export default TagTopArtistContainer;
